.root {
  display: flex;
  flex-flow: column;
  height: 100%;
  flex-grow: 1;
  background: var(--bg-color);

  .header {
    display: flex;
    height: 30px;
    min-height: 30px;
    border-top: 1px solid var(--border-color);
    background-color: var(--bg-inverse-color);
    justify-content: space-between;
    user-select: none;

    .titleContainer {
      display: flex;
      align-items: center;

      .title {
        margin: 0;
        padding: 5px 10px;
        font-size: 16px;
        color: var(--inverse-color);
        align-self: baseline;
        font-weight: normal;
        height: 100%;
      }

      .subTitle {
        flex-basis: 0;
      }
    }

    .actions {
      display: flex;
      align-self: center;

      .action {
        text-align: center;
        width: 30px;
        font-size: 16px;
        color: var(--inverse-color);
        user-select: none;
        position: relative;
        padding-right: 10px;
        align-self: center;
      }

      .action:hover {
        cursor: pointer;
        color: var(--hover-color);
      }
      
      .action:active {
        font-size: 18px;
      }
    }
  }
}