.root {
  overflow: hidden;

  .logsContainer {
    flex-grow: 1;
    overflow-x: auto;
    overflow-y: auto;
    display: flex;

    .logsList {
      margin: 0;
      padding: 0;
      list-style-type: none;
      flex-grow: 1;
  
      .log {
        padding: 4px 10px;
        color: var(--color);
        display: flex;
        border-top: 1px solid #1e1e1e;
        border-bottom: 1px solid #1e1e1e;
        white-space: pre;
        font-family: Menlo, monospace;
        align-items: flex-start;

        .logIcon {
          width: 1em;
          height: 1em;
          background-repeat: no-repeat;
          background-position: 50% 50%;
          margin-right: 10px;
        }
  
        &.debug {
          color: rgb(77, 136, 255);

          .logIcon {
            background-image: url("data:image/svg+xml;base64,")
          }
        }
  
        &.warn {
          color: rgb(245, 211, 150);
          background-color: rgb(51, 42, 0);
          border-top-color: rgb(102, 85, 0);
          border-bottom-color: rgb(102, 85, 0);

          .logIcon {
            background-image: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0naHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmcnIGZpbGw9JyNGNUQzOTYnIHZpZXdCb3g9JzAgMCA0MCA0MCc+PHBhdGggZD0nTTIxLjYgMjMuNHYtNi44aC0zLjJ2Ni44aDMuMnptMCA2LjZ2LTMuNGgtMy4yVjMwaDMuMnptLTIwIDVMMjAgMy40IDM4LjQgMzVIMS42eicvPjwvc3ZnPg==");
          }
        }
  
        &.error {
          color: rgb(254, 127, 127);
          background-color: rgb(40, 0, 0);
          border-top-color: rgb(91, 0, 0);
          border-bottom-color: rgb(91, 0, 0);

          .logIcon {
            background-image: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0naHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmcnIGZpbGw9JyNGRTdGN0YnIHZpZXdCb3g9JzAgMCA0MCA0MCc+PHBhdGggZD0nTTIxLjYgMjEuNnYtMTBoLTMuMnYxMGgzLjJ6bTAgNi44VjI1aC0zLjJ2My40aDMuMnpNMjAgMy40YTE2LjYgMTYuNiAwIDEgMSAwIDMzLjIgMTYuNiAxNi42IDAgMSAxIDAtMzMuMnonLz48L3N2Zz4=");
          }
        }

        &.info {
          .logIcon {
            background-image: url("data:image/png;base64,")
          }
        }

        .time {
          margin: 0 10px 0 0;
          display: inline-block;
        }
        
        .message {
          margin: 0;
        }
      }
    }
    
    .bottomMarker {
      /* give the marker a width/height in order for scrollIntoView to clip left  */
      height: 2px;
      width: 2px;
    }
  }
}