.context-pane {
  display: flex;
  height: 100%;
}

.react-tabs {
  display: flex;
  flex-flow: column;
  flex-grow: 1;
}

.react-tabs__tab-list {
  display: flex;
  background-color: var(--bg-secondary-color);
  border-color: var(--border-primary-color);
  margin: 0;
  min-height: 33px;
}

.react-tabs__tab {
  color: var(--muted-color);
  user-select: none;
  flex-shrink: 0;
}

.react-tabs__tab:first-child {
  margin-left: 2px;
}

.react-tabs__tab:focus {
  box-shadow: none;
  border-color: var(--border-primary-color);
  border-bottom-color: var(--bg-color);

  &::after {
    display: none;
  }
}

.react-tabs__tab-panel {
  display: none;
  flex-grow: 1;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}

.react-tabs__tab-panel--selected {
  display: flex;
  background-color: var(--bg-color);
}

.react-tabs__tab--selected {
  border-color: var(--border-primary-color);
  border-radius: 0;
  background-color: var(--bg-color);
  color: var(--primary-color);
  border-bottom: 2px solid var(--bg-color);
}

.ReactTable {
  padding: 10px;
}

.ReactTable .rt-table {
  flex-grow: 0;
}

.ReactTable .rt-resizer {
  width: 5px;
  right: -3px;
}

.react-tabs__tab-panel--selected .empty {
  color: var(--muted-color);
  margin: auto;
  font-size: 18px;
  align-self: center;
}

.react-tabs__tab-panel--selected .empty::after {
  content: "<empty>";
}

.charts-container {
  position: relative;
  height: 100%;
  flex-grow: 1;
  padding: 15px;
  display: flex;
  flex-flow: column;
  justify-content: center;
}

.chart-container {
  margin-bottom: 15px;
}

.chart-header {
  display: flex;
  justify-content: space-between;
  color: var(--color);
  background-color: var(--bg-primary-color);
  padding: 5px;
}

.chart-title {
  font-weight: bold;
}

.chart-actions {
  display: flex;
  flex-shrink: 0;
  justify-content: flex-end;

  i {
    padding-right: 10px;

    &:last-child {
      padding-right: 0;
    }
  }
}

.chart-content {
  height: 250px;
  width: 100%;
}