.section {
  display: flex;
  flex-flow: column;

  .headerContainer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 26px;
    padding: 5px 8px 5px 12px;
    background-color: var(--bg-third-color);
    border-top: 1px solid var(--border-color);
    user-select: none;
    box-shadow: none;

    &:hover {
      background: rgba(0, 0, 0, 0.2);
      cursor: pointer;
    }

    &:active {
      background: rgba(0, 0, 0, 0.3);
    }

    &.open {
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
    }

    .header {
      display: flex;
      align-items: center;
      flex-shrink: 0;
      flex-grow: 2;
      height: 16px;
      padding-right: 8px;

      .icon {
        line-height: 16px;
        display: flex;
        min-width: 30px;
        text-align: left;
      }

      .title {
        line-height: 16px;
        display: flex;
        margin: 0;
        font-size: 15px;
        flex-shrink: 0;
      }

      .foldIcon {
        line-height: 16px;
      }
    }
  }

  .hide {
    display: none;
  }
}