body {
  /* Default theme */
  --bg-color: #fff;
  --bg-primary-color: #fff;
  --bg-secondary-color: #ddd;
  --bg-third-color: #fff;
  --color: #000;
  --primary-color: #222;
  --secondary-color: #333;
  --fancy-color: #3ba3cc;
  --border-color: #555;
  --border-primary-color: #666;
  --border-secondary-color: #777;
  --hover-color: #ccc;
  --inverse-color: #fff;
  --bg-inverse-color: #333;
  --bg-inverse-primary-color: #222;
  --bg-action: #eee;
  --bg-action-active: #ccc;
  --muted-color: #666;
  --muted-primary-color: #999;
  --selection-color: #000;
  --bg-selection-color: #bbb;
}

body.night {
  /* Night theme override */
  --bg-color: #222;
  --bg-primary-color: #1e1e1e;
  --bg-secondary-color: #262626;
  --bg-third-color: #141414;
  --color: #fff;
  --primary-color: #ccc;
  --secondary-color: #999;
  --fancy-color: #3ba3cc;
  --border-color: #333;
  --border-primary-color: #444;
  --border-secondary-color: #494949;
  --hover-color: #ccc;
  --inverse-color: #fff;
  --bg-inverse-color: #141414;
  --bg-inverse-primary-color: #222;
  --bg-action: #242424;
  --bg-action-active: #1e1e1e;
  --muted-color: #666;
  --muted-primary-color: #777;
  --selection-color: #ccc;
  --bg-selection-color: #1b1b1b;
}
.badge {
  display: inline-block;
  padding: .25em .4em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: .25rem;
  color: var(--primary-color);
  background-color: var(--bg-secondary-color);
}

.badge:empty {
  display: none;
}

.badge-pill {
  padding-right: .6em;
  padding-left: .6em;
  border-radius: 10rem;
}
.Resizer {
  background-color: var(--border-color);
  box-shadow: -1px 1px 4px rgba(0, 0, 0, 0.23), 1px 1px 4px rgba(0, 0, 0, 0.23);
  z-index: 1;
  flex-shrink: 0;
}

.Resizer:hover {
  background-color: var(--border-primary-color);
}

.Resizer.horizontal {
  height: 8px;
  cursor: row-resize;
  width: 100%;
}

.Resizer.vertical {
  width: 8px;
  cursor: col-resize;
}

.Resizer.disabled {
  cursor: not-allowed;
}

.Resizer.disabled:hover {
  border-color: transparent;
}
* {
  box-sizing: border-box;
}

.muted {
  color: var(--muted-color);
}

.flex {
  display: flex;
}

.handle-bar {
  background-color: var(--border-color);
  box-shadow: -1px 1px 4px rgba(0, 0, 0, 0.23), 1px 1px 4px rgba(0, 0, 0, 0.23);
  width: 8px;
  min-width: 8px;
}

.handle-bar.horizontal {
  width: 100%;
  min-width: 100%;
  height: 8px;
  min-height: 8px;
}

.handle-bar:hover {
  background-color: var(--border-primary-color);
}

.handle-bar:active,
.handle-bar.handle-bar_clone {
  background-color: var(--border-secondary-color);
}

.ReactModal__Content {
  background: var(--bg-color) !important;
  color: var(--primary-color) !important;
  border-color: var(--border-color) !important;
  border-radius: 0 !important;
}

.splitter .pane {
  background-color: var(--bg-color);
}
