.Resizer {
  background-color: var(--border-color);
  box-shadow: -1px 1px 4px rgba(0, 0, 0, 0.23), 1px 1px 4px rgba(0, 0, 0, 0.23);
  z-index: 1;
  flex-shrink: 0;
}

.Resizer:hover {
  background-color: var(--border-primary-color);
}

.Resizer.horizontal {
  height: 8px;
  cursor: row-resize;
  width: 100%;
}

.Resizer.vertical {
  width: 8px;
  cursor: col-resize;
}

.Resizer.disabled {
  cursor: not-allowed;
}

.Resizer.disabled:hover {
  border-color: transparent;
}