:global {
  .monaco-menu-container .action-item:not(.disabled):hover {
    background-color: var(--bg-secondary-color);
  }

  .monaco-editor, .monaco-editor-background, .monaco-editor .inputarea.ime-input, .monaco-editor .margin {
    background-color: var(--bg-primary-color) !important;
  }
}

.root {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: column;
  flex-grow: 1;
  overflow: hidden;

  .header {
    min-height: 40px;
    display: flex;
    justify-content: space-between;
    background-color: var(--bg-secondary-color);
  }
  
  .fileTabs {
    display: flex;
  }
  
  .tab {
    padding: 5px 15px;
    display: flex;
    justify-content: space-between;
    border-top: 2px solid transparent;
    background-color: var(--bg-secondary-color);

    &:hover {
      background-color: var(--bg-color);
      cursor: pointer;

      .icon {
        visibility: visible;
      }
    }

    &:active {
      background-color: var(--bg-primary-color);
    }

    &.selected {
      border-top: 2px solid var(--fancy-color);
      background-color: var(--bg-primary-color);

      .name {
        color: var(--primary-color);
      }
    }

    .languageIcon {
      padding-right: 5px;
    }

    .name {
      color: var(--muted-color);
      padding: 0 5px;
      align-self: center;
      user-select: none;
    }
    
    .icon {
      visibility: hidden;
      padding-left: 15px;
      user-select: none;
      align-self: center;
    }
    
    .icon i:hover::before {
      color: lightsalmon;
    }
  }
  
  .monacoContainer {
    position: relative;
    display: flex;
    flex-grow: 1;
    height: 100%;
    padding: 5px 0 2px 0;
    background-color: var(--bg-primary-color);
  }
  
  .monacoGreycat {
    background-color: var(--bg-primary-color);
    flex-grow: 1;
    width: 100%;
    height: 100%;
  }
  
  .noWorkspace {
    position: absolute;
    display: flex;
    height: 100%;
    width: 100%;

    .center {
      width: 100%;
      align-self: center;
      text-align: center;

      p {
        font-size: 14px;
        line-height: 14px;

        code {
          background-color: var(--bg-third-color);
          padding: 2px 5px;
        }
      }
    }
  }
}

.run {
  color: #89d185 !important;
}
.run:hover {
  color: #5da559 !important;
}
.run:active {
  color: #2e7e32 !important;
}