.languageIcon {
  width: 22px;
  height: 22px;
  align-self: center;
  display: flex;

  &.greycat {
    svg {
      width: 19px;
      height: 19px;
      align-self: center;

      path {
        fill: #666;
      }
    }
  }

  &.typescript {
    svg {
      width: 24px;
      height: 24px;
      align-self: center;

      path {
        fill: #0070bc;
      }
    }
  }
}